.page_404 {
	width: 97%;
	margin:auto;
}

.errorPage {
	text-align: center;
	color: #FF6060;
}

.errorPage h1 {
	font-size: 290px;
	font-weight: bold;
	margin-top: 280px;
	margin-bottom: 70px;
}

.errorPage h2 {
	font-size: 35px;
	margin-bottom: 180px;
}

.errorPage p{
	font-size: 18px;
	margin-bottom: 70px;
}

@media screen and (max-width: 768px) {
	.errorPage h1 {
		font-size: 120px;
	}

	.errorPage h2 {
		font-size: 20px;
	}
}

@media screen and (min-width:769px) and (max-width: 1024px) {
	.errorPage h1 {
		font-size: 180px;
	}

	.errorPage h2 {
		font-size: 25px;
	}
}